import swiperTabHead from "@/components/home/tabBars.vue"
import tabList from "@/components/home/tab-list.vue"
export default {
	components: {
		swiperTabHead,
		tabList
	},
	data() {
		return {
			tabIndex: 0,
			tabBars: [{
				name: '最新热品',
				n: 'zuixinrepin'
			}, {
				name: '最新上架',
				id: 'zuixinshangjia'
			}, {
				name: '即将开售',
				id: 'jijiangkaishou'
			}, {
				name: '新品手机',
				id: 'xinpinshouji'
			}],
			newslist: ''
		}
	},
	created() {
		this.shuj();
	},
	methods: {
		async shuj() {
			let [error, res] = await uni.request({
				url: 'http://ceshi3.dishait.cn/api/index_category/data' //接口拿取数据
			})
			this.newslist = res.data.data.data[4].data //每日精选商品
		},
		tabChange(e) {
			this.tabIndex = e.detail.current;
		},
		tabtap(index) {
			this.tabIndex = index;
		}
	}
}
